// components/FileUpload.tsx

'use client'

import React from 'react'

interface FileUploadProps {
  label: string
  name: string
  required?: boolean
  onChange: (e: React.ChangeEvent<HTMLInputElement>) => void
  error?: string
}

export default function FileUpload({
  label,
  name,
  required = false,
  onChange,
  error,
}: FileUploadProps) {
  return (
    <div className="mb-4">
      <label htmlFor={name} className="block text-sm font-medium text-gray-700 mb-1">
        {label}{required && <span className="text-red-500"> *</span>}
      </label>
      <input
        type="file"
        name={name}
        id={name}
        onChange={onChange}
        required={required}
        className={`w-full px-3 py-2 border rounded-md text-sm text-gray-700 file:mr-4 file:py-2 file:px-4 file:rounded-md file:border-0 file:text-sm file:font-semibold file:bg-blue-50 file:text-blue-700 hover:file:bg-blue-100 ${
          error ? 'border-red-500' : 'border-gray-300'
        }`}
      />
      {error && <p className="text-sm text-red-500 mt-1">{error}</p>}
    </div>
  )
}
